.fuel-admin-container {
  padding: 20px;
  font-family: 'Segoe UI', sans-serif;
}

.fuel-admin-title {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 20px;
}

.form-section {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}

.form-left {
  flex: 2;
  padding-right: 20px;
}

.form-right {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

.upload-icon {
  width: 100px;
  height: 100px;
  border-radius: 10px;
  object-fit: cover;
}

.upload-section {
  margin-bottom: 20px;
  text-align: center;
}


.fuel-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.fuel-table th,
.fuel-table td {
  padding: 12px 15px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.fuel-table th {
  background-color: #f2f2f2;
  font-weight: bold;
}

.fuel-table tr:hover {
  background-color: #f9f9f9;
}

.status-approved {
  color: green;
  font-weight: bold;
}

.status-rejected {
  color: red;
  font-weight: bold;
}

.status-pending {
  color: #888;
  font-style: italic;
}

.fuel-preview-link {
  color: blue;
  text-decoration: underline;
  cursor: pointer;
}

@media (max-width: 768px) {
  .fuel-table {
    font-size: 14px;
  }

  .fuel-table th,
  .fuel-table td {
    padding: 10px;
  }
}
